import star from '@/base/star/star'
import tools from '@/common/tools.js'
import axios from 'axios'
import uploadimg from '@/base/uploadimg/uploadimg'

export default {
  name: 'gotoassess',
  data: function () {
    return {
      miaoshuValue: 0,
      wuliuValue: 0,
      orderId: '',
      allData: [],
      commonList: []
    }
  },
  watch: {
    '$route' (to, from) { // 监听路由是否变化
      if (to.name === 'gotoassess') {
        this.orderId = this.$route.query.orderId
        console.log(this.orderId)
        this.resetFunc()
        // this.baoyou = this.$route.query.post
        if (!!this.orderId) {
          this._getAssessData()
        }
      }
    }
  },
  activated() {
    this.orderId = this.$route.query.orderId
    console.log(this.orderId)
    this._getAssessData()
  },
  methods: {
    resetFunc() {
      this.miaoshuValue = 0
      this.wuliuValue = 0
      this.orderId = ''
      this.allData = []
      this.commonList = []
    },
    getUploadImgArr(arr, index) {
      console.log('我的数字是：', arr, index)
      this.commonList[index].comment_img = arr
    },
    sendAssessData() {
      for (let i in this.commonList) {
        console.log(i)
        if (this.commonList[i].content == '') {
          tools.showError('评论不能为空')
          return false
          break
        }
      }
      if (this.miaoshuValue == 0) {
        tools.showError('请选择描述评级')
        return false
      }
      if (this.wuliuValue == 0) {
        tools.showError('请选择物流评级')
        return false
      }
      let url = '/Mobile/Order/add_comment.html'
      axios.post(url, {
        deliver_rank: this.wuliuValue,
        goods_rank: this.miaoshuValue,
        comment: this.commonList
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            tools.showError(res.data.msg)
            setTimeout(() => {
              this.$router.go(-1)
            }, 1000)
          } else {
            tools.showError(res.data.msg)
            /* setTimeout(() => {
              this.$router.go(-1)
            }, 1000) */
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    monishuju(data) {
      /* {//评价数据,评价等级一颗星代表1
        "order_id":"订单ID",
        "goods_id":"商品ID",
        "content":"评价内容",
        "comment_img":[{//评价图片键值用数字索引就行
        "0":"第一张图片",
        "1":"第二张图片",
      } */
      for (let i in data) {
        this.commonList.push({
          'order_id': data[i].order_id,
          'goods_id': data[i].goods_id,
          'content': '',
          'comment_img': []
        })
      }
      console.log(this.commonList)
    },
    _getAssessData() {
      let url = '/Mobile/Order/add_comment.html'
      axios.get(url, {
        params: {
          order_id: this.orderId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            this.allData = res.data.data
            this.monishuju(this.allData)
          } else {
            tools.showError(res.data.msg)
            setTimeout(() => {
              this.$router.go(-1)
            }, 1000)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  components: {
    star,
    uploadimg
  }
}



