export default {
  name: 'star',
  data: function() {
    return {
      starNum: 5,
      nowStarIndex: 0,
      starTxt: ['非常差', '差', '一般', '好', '非常好'],
      nowStarTxt: ''
    }
  },
  props: {
    selectStarNum: Number
  },
  model: {
    prop: 'selectStarNum',
    event: 'returnBack'
  },
  methods: {
    selectStar(index) {
      this.nowStarIndex = index
      this.nowStarTxt = this.starTxt[index - 1]
      this.$emit('returnBack', index)
    }
  }
}



